<template>
  <main>
    <HeaderComponent></HeaderComponent>
    <section class="main" :style="'min-height:calc(' + $store.state.wh / 2 + 'px - 80px)'">
      <h1>Busque pelos produtos com menor preço</h1>
      <input type="text" id="input" v-model="query" @keyup="search" />
      <button class="av" @click="search">Pesquisa avançada</button>

    </section>
    <div class="cardWrapper">
      <div class="card" :key="n + i" v-for="(n, i) in results.length > 0 ? results : gpusRaw">
        <a :href="n.link" target="_blanc">
          <div class="cardImage">
            <img :src="n.img" alt="" />
          </div>
          <div class="cardTitle" v-if="gpusRaw.length > 0">
            {{
              n.brand + ' ' + n.manufacturer +
              " " +
              n.model
            }}<br /><span style="color: darkgreen; font-weight: 600">Preço: {{ n.price }}</span>
          </div>
          <p>{{ n.store }}</p>
        </a>
      </div>
    </div>
  </main>
</template>


<script>
import HeaderComponent from "../components/HeaderComponent.vue";
import axios from "axios";
export default {
  data() {
    return {
      gpusRaw: [],
      results: [],
      query: "",
    };
  },
  mounted() {
    const self = this;
    axios.get("https://cpback.agreablecode.com/api/gpu").then((s) => {


      self.gpusRaw = s.data.sort((a, b) => {
        return a.price - b.price;
      });
      if (self.query != '') {
        self.search()
      }
    });
  },

  methods: {
    search() {
      let query = this.query.trim().replace(" ti", "ti");
      if (!query) {
        this.results = [];
        return;
      }
      // `(?=.*\\bgalax\\b)(?=.*\\b4070\\b)`
      let regex = query.split(" ");
      let reg = "";
      regex.forEach((element) => {
        if (element != undefined) {
          reg += "(?=.*" + element + ")";
        }
      });
      // console.log(reg)
      let pattern = new RegExp(reg.replace(/\s+/g, "\\s+"), "gi");
      this.results = this.gpusRaw.filter((product) =>
        pattern.test(
          product.model.replaceAll(" ", "") + product.brand.replaceAll(" ", "") + product.manufacturer.replaceAll(" ", "") + product.store.replaceAll(" ", "") + product.price.replaceAll(" ", "")
        )
      );
      this.results.sort((a, b) => {
        return a.price - b.price;
      });
    },
  },
  beforeUnmount() { },
  components: {
    HeaderComponent,
  },
};
</script>

