import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import GpuView from '../views/GpuViewcopy.vue'
import ProcessorView from '../views/ProcessorView.vue'
import PcBuildView from '../views/PcBuild.vue'
import ProductsView from '../views/ProductsView.vue'
import CasesView from '../views/CaseView.vue'
import MotherboardsView from '../views/MotherboardView.vue'
import SsdView from '../views/SsdView.vue'
import MemoryView from '../views/memoryView.vue'
import PowersuplyView from '../views/PowersuplyView.vue'
const routes = [
    {
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/gpu',
        name: 'gpu',
        component: GpuView
    },
    {
        path: '/processors',
        name: 'processor',
        component: ProcessorView
    },
    {
        path: '/pcbuild',
        name: 'pcbuild',
        component: PcBuildView
    },
    {
        path: '/products',
        name: 'products',
        component: ProductsView
    },
    {
        path: '/cases',
        name: 'cases',
        component: CasesView
    },
    {
        path: '/motherboards',
        name: 'motherboards',
        component: MotherboardsView
    },
    {
        path: '/ssd',
        name: 'ssd',
        component: SsdView
    },
    {
        path: '/memory',
        name: 'memory',
        component: MemoryView
    },
    {
        path: '/powersuply',
        name: 'powersuply',
        component: PowersuplyView
    },
    
    
]

const router = createRouter({
    mode: "history",
    history: createWebHistory(),
    routes
})

export default router


