<template>
  <main>
    <HeaderComponent></HeaderComponent>
    <section
      class="main"
      :style="'min-height:calc(' + $store.state.wh / 2 + 'px - 80px)'"
    >
      <h1>Busque pelos produtos com menor preço</h1>
      <input type="text" id="input" v-model="query" @keyup="search" />
      <button class="av" @click="search">Pesquisa avançada</button>

      <!-- {{ MotherboardsRaw || '' }} -->
    </section>
    <!-- <table>
      <tr>
        <th>modelo</th>
        <th>preço</th>
        <th>preço a prazo</th>
        <th>lojas</th>
        <th>cores</th>
        <th>threads</th>
        <th>clock min</th>
        <th>clock max</th>
        <th>consumo minimo</th>
      </tr>
      <tr v-for="(p,n) in results" :key="n">
        <td>{{ p.model }}</td>
        <td>R$ {{ p.price }}</td>
        <td>{{ p.cores }}</td>
        <td>{{ p.threads }}</td>
        <td>{{ p.ghzmin }}</td>
        <td>{{ p.ghzmax }}</td>
        <td>{{ p.consumptionMin }}</td>
      </tr>
    </table> -->
    <div class="cardWrapper">
      <!-- {{ MotherboardsRaw[0] }} -->
      <div
        class="card"
        :key="n+i"
        v-for="(n,i) in results.length > 0 ? results : MotherboardsRaw"
      >
        <a :href="n.link" target="_blanc">
          <div class="cardImage" :style="'background:url('+n.img+');'">
          </div>
          <div class="cardTitle" v-if="MotherboardsRaw.length > 0">
            {{
             n.brand+ ' '+n.manufacturer+' '+n.model+' socket: '+n.socket
            }}<br /><span style="color: darkgreen; font-weight: 600"
              >Preço: {{ n.price }}</span
            >
          </div>
          <p>{{ n.store }}</p>
        </a>
      </div>
    </div>
  </main>
</template>


<script>
import HeaderComponent from "../components/HeaderComponent.vue";
import axios from "axios";
export default {
  data() {
    return {
      MotherboardsRaw: [],
      results: [],
      teste: [
        {
          id: 40,
          title: null,
          originalTitle:
            "Processador Intel Core i3-10105F, 4-Core, 8-Threads, 3.7Ghz (4.4Ghz Turbo) Cache 6MB, LGA1200, BX8070110105F",
          model: "i3 10105F",
          price: "399.00",
          pricep: "469.44",
          store: "pichau",
          link: "https://www.pichau.com.br/processador-intel-core-i3-10105f-4-core-8-threads-3-7ghz-4-4ghz-turbo-cache-6mb-lga1200-bx8070110105f",
          img: "",
          availability: 1,
          created_at: "2023-03-27T21:19:45.000000Z",
          updated_at: "2023-03-30T22:51:35.000000Z",
          brand: "intel",
          cores: 4,
          threads: 8,
          geracao: "10ª geração",
          socket: "1200",
          consumptionMin: 65,
          consumptionMax: null,
          maxmemoryspeedd4: 2666,
          maxmemoryspeedd5: null,
          ghzmin: 3.7,
          ghzmax: 4.4,
        },
      ],
      query: "",
    };
  },
  mounted() {
    const self = this;
    axios.get("https://cpback.agreablecode.com/api/motherboard").then((s) => {
      
      self.MotherboardsRaw = s.data.sort((a, b) => {
        return a.price - b.price;
      });
    });
  },

  methods: {
    search() {
      let query = this.query.trim().replace(" ti", "ti");
      if (!query) {
        this.results = [];
        return;
      }
      // `(?=.*\\bgalax\\b)(?=.*\\b4070\\b)`
      let regex = query.split(" ");
      let reg = "";
      regex.forEach((element) => {
        if (element != undefined) {
          reg += "(?=.*" + element + ")";
        }
      });
      // console.log(reg)
      let pattern = new RegExp(reg.replace(/\s+/g, "\\s+"), "gi");
     
      this.results = this.MotherboardsRaw.filter((product) =>
        pattern.test(
          product.title.replaceAll(" ", "") +product.store.replaceAll(" ", "") 
        )
      );
      this.results.sort((a, b) => {
        return a.price - b.price;
      });
    },
  },
  beforeUnmount() {},
  components: {
    HeaderComponent,
  },
};
</script>

