<template>
  <div>
    <div v-for="(c,i) in cases" :key="c.id+i" style="border-bottom:2px solid red"><img :src="c.img" style='height:200px;width:200px' alt="">{{ c.title }}</div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
data(){return{
    cases:[]
}},
beforeCreate() {},
created() {},
beforeMount() {},
mounted() {
    const self = this 
    axios.get('https://cpback.agreablecode.com/api/cases').then((s)=>{
        self.cases = s.data
    })
},
beforeUpdate() {},
updated() {},
beforeUnmount() {},
unmounted() {},
computed() {},
methods: {},
}
</script>

