<template>
  <main>
    <h1>escolha sua configuração</h1>
    <v-select style="width: 700px;overflow-x: visible;"
      :options="productsList"
      v-model="escolha"
      label="title"
      ></v-select>
    {{ escolha.title }}
    {{ productsList.length }}
  </main>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      productsList: [],
      escolha: "",
    };
  },
  mounted() {
    const self = this;
    axios.get("/api/gpu").then((s) => {
      self.productsList = s.data;
      self.productsList.sort((a, b) => {
        if (a.toLowerCase() < b.toLowerCase()) {
          return -1;
        } else if (a.toLowerCase() > b.toLowerCase()) {
          return 1;
        } else {
          return 0;
        }
      });
    });
  },
  computed() {},
  methods: {},
};
</script>

<style lang="scss" scoped>
main {
  display: flex;
  align-items: center;
  width: 100vw;
  height: 50vh;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}
</style>